.skills{
    display: flex;
    text-align: left;
    padding: 10px 0 px;
    height: 50px;    
    width: 1000px;
    margin: 5px 0px;
    padding-bottom: 5px;
    border-bottom: 1px solid orangered;

}
.content-title{
    display: inline-flex;
    flex-direction: row;
    flex-wrap: wrap;
    font-size: 30px;
    width: 150px;
    padding: 0;
    margin: 0;
    overflow: visible;
    float:right;
}
.content-container{
    display: inline-flex;
    flex-direction: row;
    
    font-size: 25px;
    margin: 2px;
       
}
.icon{
    margin-left: 10px;
    filter: brightness(0) invert(1);
}
.content-text{
    display: inline-flex;
    margin-left: 15px;
    margin-top: 5px;
    margin-bottom: 0;
    height: inherit;
 
}